import classNames from 'classnames/bind'
import styles from './Footer.module.scss'
import { logo } from '../../../assets/images'
import { facebook, email, youtube } from '../../../assets/icons'

const cx = classNames.bind(styles);

function Footer() {
  return (
    <footer className={cx('wrapper')}>
      <div className={cx('container')}>
        <div className={cx('inner')}>
          <div className={cx('info')}>
            <div className={cx('logo')}>
              <img src={logo} alt="logo"></img>
            </div>
            <p className={cx('name')}>CNG TY C PHN SIMPLIZE</p>
            <p className={cx('mst')}>MST: 0109620361</p>
            <p className={cx('address')}>S 25 ng 198 Thi H, P. Trung Lit, Q. ng a, TP. H Ni, Vit Nam</p>
            <p className={cx('copyright')}>Copyright  2021 Simplize | Powered by Simplize</p>
          </div>

          <div className={cx('contact')}>
            <div className={cx('about-rules')}>
              <span className={cx('about-us')}>V chng ti</span>
              <span className={cx('rules')}>iu khon s dng</span>
            </div>

            <div className={cx('packet-policy')}>
              <span className={cx('packet')}>Hi vin</span>
              <span className={cx('policy')}>Chnh sch bo mt</span>
            </div>

            <div className={cx('contact-socials')}>
              <span className={cx('contact-us')}>Lin h vi chng ti!</span>
              <div className={cx('socials')}>
                <div className={cx('social-icon', 'facebook')}>
                  <img src={facebook} alt=""></img>
                </div>
                <div className={cx('social-icon', 'email')}>
                  <img src={email} alt=""></img>
                </div>
                <div className={cx('social-icon', 'youtube')}>
                  <img src={youtube} alt=""></img>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </footer>
  );
}

export default Footer;